/**
 * 
 *  maintext 
 */

declare function getChatMessages(
  range: string | number,
  options?: { role?: 'all' | 'system' | 'assistant' | 'user' },
): Array<{ message_id: number; role: string; message: string }>;
declare function getLastMessageId(): number;
import { parseMaintext } from './messageParser';

export interface ReadingModeEntry {
  messageId: number;
  floorNumber: number;
  preview: string;
  fullText: string;
}

/**
 * 
 */
function getFirstFewSentences(text: string, maxSentences: number = 3): string {
  if (!text) return '';
  
  // 
  const sentences = text.split(/[.!?]/).filter(s => s.trim().length > 0);
  
  if (sentences.length <= maxSentences) {
    return text;
  }
  
  return sentences.slice(0, maxSentences).join('') + '...';
}

/**
 * 
 */
export function getReadingModeData(): ReadingModeEntry[] {
  try {
    const lastMessageId = getLastMessageId();
    if (lastMessageId < 0) return [];
    
    const entries: ReadingModeEntry[] = [];
    
    //  assistant 
    const messages = getChatMessages('0-{{lastMessageId}}', { role: 'assistant' }) as any;
    if (!messages || messages.length === 0) return [];
    
    for (const message of messages) {
      const maintext = parseMaintext(message.message || '');
      if (maintext) {
        const floorNumber = Math.floor(message.message_id / 2);
        entries.push({
          messageId: message.message_id,
          floorNumber,
          preview: getFirstFewSentences(maintext, 3),
          fullText: maintext,
        });
      }
    }
    
    // 
    entries.sort((a, b) => a.floorNumber - b.floorNumber);
    
    return entries;
  } catch (error) {
    console.error(' [readingMode] :', error);
    return [];
  }
}
