/**
 * 
 *  sum 
 */

declare function getChatMessages(
  range: string | number,
  options?: { role?: 'all' | 'system' | 'assistant' | 'user' },
): Array<{ message_id: number; role: string; message: string }>;
declare function getLastMessageId(): number;
declare function triggerSlash(command: string): Promise<string>;
import { parseSum } from './messageParser';

export interface SaveEntry {
  messageId: number;
  floorNumber: number;
  sum: string;
  timestamp?: string;
}

/**
 * sum
 */
export function getSaveData(): SaveEntry[] {
  try {
    const lastMessageId = getLastMessageId();
    if (lastMessageId < 0) return [];
    
    const entries: SaveEntry[] = [];
    
    //  assistant 
    const messages = getChatMessages('0-{{lastMessageId}}', { role: 'assistant' }) as any;
    if (!messages || messages.length === 0) return [];
    
    for (const message of messages) {
      const sum = parseSum(message.message || '');
      if (sum) {
        const floorNumber = Math.floor(message.message_id / 2);
        entries.push({
          messageId: message.message_id,
          floorNumber,
          sum,
        });
      }
    }
    
    // 
    entries.sort((a, b) => a.floorNumber - b.floorNumber);
    
    return entries;
  } catch (error) {
    console.error(' [saveLoad] :', error);
    return [];
  }
}

/**
 * 
 */
export async function createBranchAtFloor(messageId: number): Promise<boolean> {
  try {
    console.log(` [saveLoad]  ${messageId} ...`);
    
    //  /branch-create 
    const result = await triggerSlash(`/branch-create ${messageId}`);
    
    if (result) {
      console.log(` [saveLoad]  ${messageId} `);
      return true;
    } else {
      console.warn(` [saveLoad] `);
      return false;
    }
  } catch (error) {
    console.error(` [saveLoad]  ${messageId} :`, error);
    return false;
  }
}
