/**
 * 
 *  UpdateVariable  JSONPatch  _.set 
 */

export interface JSONPatchOperation {
  op: 'replace' | 'add' | 'remove' | 'insert';
  path: string;
  value?: any;
}

/**
 *  UpdateVariable  JSONPatch
 */
export function parseJSONPatch(updateVariable: string): JSONPatchOperation[] {
  const patches: JSONPatchOperation[] = [];
  
  try {
    //  3 
    // 1) <JSONPatch>[...]</JSONPatch>
    // 2)  [...]
    // 3) ```json\n[...]\n```
    const jsonPatchMatch = updateVariable.match(/<JSONPatch>([\s\S]*?)<\/JSONPatch>/i);
    let jsonPatchContent = (jsonPatchMatch ? jsonPatchMatch[1] : updateVariable).trim();

    // 
    jsonPatchContent = jsonPatchContent
      .replace(/^\s*```(?:json)?\s*/i, '')
      .replace(/\s*```\s*$/i, '')
      .trim();

    //  UpdateVariable  JSON
    if (!jsonPatchContent.startsWith('[')) return patches;

    const parsed = JSON.parse(jsonPatchContent);
    
    if (Array.isArray(parsed)) {
      return parsed as JSONPatchOperation[];
    }
  } catch (error) {
    console.warn(' [variableParser]  JSONPatch :', error);
  }
  
  return patches;
}

/**
 *  UpdateVariable  _.set 
 */
export function parseLodashSet(updateVariable: string): Array<{ path: string; value: any }> {
  const sets: Array<{ path: string; value: any }> = [];
  
  try {
    //  _.set('path', value) 
    const lodashSetRegex = /_\.set\(['"]([^'"]+)['"]\s*,\s*([\s\S]*?)\)/g;
    let match;
    
    while ((match = lodashSetRegex.exec(updateVariable)) !== null) {
      const path = match[1];
      let value = match[2].trim();
      
      // 
      try {
        // 
        value = value.replace(/;\s*$/, '').trim();
        
        // 
        if (value.startsWith('[') || value.startsWith('{')) {
          value = JSON.parse(value);
        } else if (value.startsWith('"') && value.endsWith('"')) {
          // 
          value = JSON.parse(value);
        } else if (value === 'true' || value === 'false') {
          // 
          value = value === 'true';
        } else if (!isNaN(Number(value)) && value.trim() !== '') {
          // 
          value = Number(value);
        }
        // 
      } catch (e) {
        // 
        console.warn(` [variableParser]  _.set : ${path} = ${value}`, e);
      }
      
      sets.push({ path, value });
    }
  } catch (error) {
    console.warn(' [variableParser]  _.set :', error);
  }
  
  return sets;
}

/**
 *  JSONPatch 
 */
export function applyJSONPatch(obj: any, patches: JSONPatchOperation[]): any {
  const result = JSON.parse(JSON.stringify(obj)); // 
  
  for (const patch of patches) {
    try {
      const pathParts = patch.path.split('/').filter(p => p !== '');
      
      if (patch.op === 'replace' || patch.op === 'add') {
        let current = result;
        for (let i = 0; i < pathParts.length - 1; i++) {
          const key = pathParts[i];
          if (!current[key]) {
            current[key] = {};
          }

          //   MVU 
          //  current[key]  [, ""]
          //  0 
          if (Array.isArray(current[key])) {
            if (!current[key][0] || typeof current[key][0] !== 'object') {
              current[key][0] = {};
            }
            current = current[key][0];
          } else {
            current = current[key];
          }
        }
        const lastKey = pathParts[pathParts.length - 1];
        current[lastKey] = patch.value;
      } else if (patch.op === 'insert') {
        const isContactsRoot =
          pathParts.length >= 1 &&
          (pathParts[0] === '' || pathParts[0] === '' || pathParts[0] === 'contacts');

        //   //<>/ insert 
        //  { : [ {..}, {..} ] } 
        if (isContactsRoot && pathParts.length >= 3) {
          let current = result;
          for (let i = 0; i < pathParts.length - 1; i++) {
            const key = pathParts[i];
            if (!current[key] || typeof current[key] !== 'object') {
              current[key] = {};
            }

            // MVU: [, ""]   0 
            if (Array.isArray(current[key])) {
              if (!current[key][0] || typeof current[key][0] !== 'object') {
                current[key][0] = {};
              }
              current = current[key][0];
            } else {
              current = current[key];
            }
          }

          const lastKey = pathParts[pathParts.length - 1];
          const prevVal = current[lastKey];

          // 
          const foldToObject = (v: any): Record<string, any> => {
            if (!v) return {};
            if (typeof v === 'object' && !Array.isArray(v)) return v;
            if (Array.isArray(v)) {
              const out: Record<string, any> = {};
              for (const it of v) {
                const actual = Array.isArray(it) && it.length > 0 ? it[0] : it;
                if (actual && typeof actual === 'object' && !Array.isArray(actual)) {
                  for (const [k, val] of Object.entries(actual)) {
                    out[k] = val;
                  }
                }
              }
              return out;
            }
            return {};
          };

          //  key  value 
          if (lastKey === '' && patch.value && typeof patch.value === 'object' && !Array.isArray(patch.value)) {
            const base = foldToObject(prevVal);
            for (const [k, v] of Object.entries(patch.value)) {
              if (!(k in base) || base[k] === v) {
                base[k] = v;
              } else {
                // 
                base[k] = v;
              }
            }
            current[lastKey] = base;
          } else if (patch.value && typeof patch.value === 'object' && !Array.isArray(patch.value)) {
            // 
            const base = foldToObject(prevVal);
            current[lastKey] = { ...base, ...(patch.value as any) };
          } else {
            // 
            current[lastKey] = patch.value;
          }

          continue;
        }

        //   insert /
        // 1) //
        // 2) /
        // 3) / /
        //  MVU  [, ""]pick()  0 
        const isInventoryItemsPath =
          (pathParts.length >= 2 &&
            ((pathParts[pathParts.length - 2] === '' && pathParts[pathParts.length - 1] === '') ||
              (pathParts[pathParts.length - 2] === 'inventory' && pathParts[pathParts.length - 1] === 'items')));
        const isEntityArchivePath =
          (pathParts.length === 1 &&
            (pathParts[0] === '' || pathParts[0] === 'entities'));
        const isContactsArchivePath =
          (pathParts.length === 1 &&
            (pathParts[0] === '' ||
              pathParts[0] === '' ||
              pathParts[0] === 'contacts'));

        if (isInventoryItemsPath || isEntityArchivePath || isContactsArchivePath) {
          let current = result;
          for (let i = 0; i < pathParts.length - 1; i++) {
            const key = pathParts[i];
            if (!current[key] || typeof current[key] !== 'object') {
              current[key] = {};
            }

            // MVU  [, ""]
            //  0 
            //  pick() 
            if (Array.isArray(current[key])) {
              if (current[key].length === 0 || !current[key][0] || typeof current[key][0] !== 'object') {
                current[key][0] = {};
              }
              current = current[key][0];
            } else {
              current = current[key];
            }
          }
          const lastKey = pathParts[pathParts.length - 1];

          // 
          let container: any = current[lastKey];
          if (!container) container = {};
          if (Array.isArray(container)) {
            const folded: Record<string, any> = {};
            for (const it of container) {
              if (!it) continue;
              const actual = Array.isArray(it) && it.length > 0 ? it[0] : it;
              if (actual && typeof actual === 'object') {
                for (const [k, v] of Object.entries(actual)) {
                  folded[k] = v;
                }
              }
            }
            container = folded;
          } else if (typeof container !== 'object') {
            container = {};
          }

          // patch.value 
          // - { "<>": { , , ... } }
          // - { "<>": { , , , , ... } }
          // - { "</ID>": { ... } }
          if (patch.value && typeof patch.value === 'object' && !Array.isArray(patch.value)) {
            current[lastKey] = { ...(container as any), ...(patch.value as any) };
          } else {
            // 
            current[lastKey] = container;
          }
          continue;
        }

        let current = result;
        for (let i = 0; i < pathParts.length - 1; i++) {
          const key = pathParts[i];
          if (!current[key]) {
            // 
            current[key] = {};
          }
          current = current[key];
        }
        const lastKey = pathParts[pathParts.length - 1];
        // insert 
        if (!current[lastKey]) {
          current[lastKey] = [];
        }
        if (Array.isArray(current[lastKey])) {
          current[lastKey].push(patch.value);
        } else if (typeof current[lastKey] === 'object' && current[lastKey] !== null) {
          // 
          current[lastKey] = [current[lastKey], patch.value];
        } else {
          // 
          current[lastKey] = [current[lastKey], patch.value];
        }
      } else if (patch.op === 'remove') {
        let current = result;
        for (let i = 0; i < pathParts.length - 1; i++) {
          const key = pathParts[i];
          if (!current[key]) break;
          current = current[key];
        }
        const lastKey = pathParts[pathParts.length - 1];
        delete current[lastKey];
      }
    } catch (error) {
      console.warn(` [variableParser]  JSONPatch : ${patch.path}`, error);
    }
  }
  
  return result;
}

/**
 *  _.set 
 */
export function applyLodashSet(obj: any, sets: Array<{ path: string; value: any }>): any {
  const result = JSON.parse(JSON.stringify(obj)); // 
  
  for (const set of sets) {
    try {
      const pathParts = set.path.split('.');
      let current = result;
      
      for (let i = 0; i < pathParts.length - 1; i++) {
        const key = pathParts[i];
        if (!current[key]) {
          current[key] = {};
        }
        current = current[key];
      }
      
      const lastKey = pathParts[pathParts.length - 1];
      current[lastKey] = set.value;
    } catch (error) {
      console.warn(` [variableParser]  _.set : ${set.path}`, error);
    }
  }
  
  return result;
}

/**
 *  UpdateVariable 
 */
export function parseAndApplyUpdateVariable(updateVariable: string, baseData: any): any {
  if (!updateVariable || !baseData) return baseData;
  
  let result = baseData;
  
  //  JSONPatch
  const patches = parseJSONPatch(updateVariable);
  if (patches.length > 0) {
    result = applyJSONPatch(result, patches);
    console.log(' [variableParser]  JSONPatch :', patches.length);
  }
  
  //  _.set 
  const sets = parseLodashSet(updateVariable);
  if (sets.length > 0) {
    result = applyLodashSet(result, sets);
    console.log(' [variableParser]  _.set :', sets.length);
  }
  
  return result;
}
