/**
 * 
 * 
 * 1.  id: <option id="A"></option>
 * 2.  id: <option>\nA. 1\nB. 2\n</option>
 */
export interface Option {
  id: string;
  text: string;
}

export function parseOptions(messageContent: string): Option[] {
  if (!messageContent) return [];
  
  const cleaned = cleanMessageContent(messageContent);
  
  //  id 
  const optionWithIdRegex = /<option id="([^"]+)">([^<]+)<\/option>/g;
  const optionsWithId: Option[] = [];
  let match;
  
  while ((match = optionWithIdRegex.exec(cleaned)) !== null) {
    optionsWithId.push({
      id: match[1],
      text: match[2].trim()
    });
  }
  
  if (optionsWithId.length > 0) {
    return optionsWithId;
  }
  
  //  id 
  const optionMatch = cleaned.match(/<option>([\s\S]*?)<\/option>/i);
  if (!optionMatch) {
    return [];
  }
  
  const optionText = optionMatch[1].trim();
  const lines = optionText.split('\n').map(line => line.trim()).filter(line => line.length > 0);
  
  //  A.B.C. 
  const optionPattern = /^[A-Z]\.\s*/;
  const hasLetterPrefix = lines.some(line => optionPattern.test(line));
  
  if (hasLetterPrefix) {
    // 
    const options: Option[] = [];
    let currentOption: string[] = [];
    
    for (const line of lines) {
      if (optionPattern.test(line)) {
        if (currentOption.length > 0) {
          const text = currentOption.join('\n');
          const id = text.match(/^([A-Z])\./)?.[1] || String.fromCharCode(65 + options.length);
          options.push({
            id,
            text: text.replace(/^[A-Z]\.\s*/, '').trim()
          });
          currentOption = [];
        }
        currentOption.push(line);
      } else {
        if (currentOption.length > 0) {
          currentOption.push(line);
        }
      }
    }
    
    if (currentOption.length > 0) {
      const text = currentOption.join('\n');
      const id = text.match(/^([A-Z])\./)?.[1] || String.fromCharCode(65 + options.length);
      options.push({
        id,
        text: text.replace(/^[A-Z]\.\s*/, '').trim()
      });
    }
    
    return options;
  } else {
    // 
    return lines.map((line, index) => ({
      id: String.fromCharCode(65 + index),
      text: line
    }));
  }
}

/**
 * 
 */
export interface ParsedMessage {
  maintext: string;
  status: string;
  task: string;
  inventory: string;
  sum: string;
  updateVariable: string;
  options: Option[];
  messageId?: number;
  userMessageId?: number;
  fullMessage?: string;
}

/**
 *  assistant 
 */
export function loadFromLatestMessage(): ParsedMessage {
  try {
    const lastMessageId = getLastMessageId();
    if (lastMessageId < 0) {
      return { 
        maintext: '', 
        status: '',
        task: '',
        inventory: '',
        sum: '',
        updateVariable: '',
        options: [] 
      };
    }

    //  assistant 
    const messages = getChatMessages(lastMessageId, { role: 'assistant' });
    let latestMessage;
    
    if (!messages || messages.length === 0) {
      // 
      const allMessages = getChatMessages(lastMessageId);
      if (!allMessages || allMessages.length === 0) {
        return { 
          maintext: '', 
          status: '',
          task: '',
          inventory: '',
          sum: '',
          updateVariable: '',
          options: [] 
        };
      }
      latestMessage = allMessages[0];
    } else {
      latestMessage = messages[0];
    }

    const messageContent = latestMessage.message || '';

    // 
    const maintext = parseMaintext(messageContent);
    const status = parseStatus(messageContent);
    const task = parseTask(messageContent);
    const inventory = parseInventory(messageContent);
    const sum = parseSum(messageContent);
    const updateVariable = parseUpdateVariable(messageContent);
    const options = parseOptions(messageContent);

    //  user 
    let userMessageId: number | undefined;
    if (latestMessage.message_id > 0) {
      const userMessages = getChatMessages(latestMessage.message_id - 1, { role: 'user' });
      if (userMessages && userMessages.length > 0) {
        userMessageId = userMessages[0].message_id;
      }
    }

    // 
    let updateVariableData: any = null;
    if (updateVariable) {
      try {
        updateVariableData = getVariables({ type: 'message', message_id: 'latest' });
      } catch (error) {
        console.warn(':', error);
      }
    }

    return { 
      maintext, 
      status,
      task,
      inventory,
      sum,
      updateVariable: updateVariable || (updateVariableData ? JSON.stringify(updateVariableData, null, 2) : ''),
      options, 
      messageId: latestMessage.message_id,
      userMessageId,
      fullMessage: messageContent
    };
  } catch (error) {
    console.error(' [messageParser] :', error);
    return { 
      maintext: '', 
      status: '',
      task: '',
      inventory: '',
      sum: '',
      updateVariable: '',
      options: [] 
    };
  }
}
