
import { Ghost, Send, Sparkles, X } from 'lucide-react';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { LogEntry, PlayerStats } from '../types';
import { loadFromLatestMessage, Option } from '../utils/messageParser';
import { handleUnifiedRequest, RequestData } from '../utils/unifiedRequestHandler';
import { readGameData } from '../utils/variableReader';

declare function getVariables(option: { type: 'global' }): Record<string, any>;
declare function getChatMessages(range: string | number, options?: { role?: 'all' | 'system' | 'assistant' | 'user' }): Array<{ message: string; message_id: number; role: string; data?: Record<string, any> }>;
declare function getLastMessageId(): number;
declare function deleteChatMessages(message_ids: number[], { refresh }?: { refresh?: 'none' | 'affected' | 'all' }): Promise<void>;
declare function setChatMessages(messages: Array<{ message_id: number; message: string }>, options?: { refresh?: 'none' | 'affected' | 'all' }): Promise<void>;

interface TerminalProps {
  logs: LogEntry[];
  addLog: (log: LogEntry) => void;
  stats: PlayerStats;
  setStats: React.Dispatch<React.SetStateAction<PlayerStats>>;
}

export const Terminal: React.FC<TerminalProps> = ({ logs, addLog, stats, setStats }) => {
  const [input, setInput] = useState('');
  const [isThinking, setIsThinking] = useState(false);
  const scrollRef = useRef<HTMLDivElement>(null);
  const [hasLoadedInitialMessage, setHasLoadedInitialMessage] = useState(false);

  // 
  const [isGenerating, setIsGenerating] = useState(false);
  const [streamingText, setStreamingText] = useState('');
  const [optionsDisabled, setOptionsDisabled] = useState(false);
  const [currentOptions, setCurrentOptions] = useState<Option[]>([]);
  const [optionsExpanded, setOptionsExpanded] = useState(false); // /
  const [currentSum, setCurrentSum] = useState<string>(''); // 

  // roll
  const [contextMenu, setContextMenu] = useState<{ x: number; y: number } | null>(null);
  const [editingMessage, setEditingMessage] = useState<{
    messageId: number;
    currentText: string;
    fullMessage: string;
  } | null>(null);
  const [currentMessageInfo, setCurrentMessageInfo] = useState<{
    messageId?: number;
    userMessageId?: number;
    fullMessage?: string;
  }>({});
  const longPressTimerRef = useRef<number | null>(null);

  // 
  useEffect(() => {
    (window as any).__currentSum = currentSum;
  }, [currentSum]);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [logs, isThinking]);

  // 
  useEffect(() => {
    if (hasLoadedInitialMessage) return;

    const loadInitialMessage = () => {
      try {
        const parsed = loadFromLatestMessage();

        // 
        const hasMaintext = logs.some(log => log.sender === 'INNER_VOICE' && log.type === 'narrative');
        if (hasMaintext && logs.length > 0) {
          setHasLoadedInitialMessage(true);
          return;
        }

        const newLogs: LogEntry[] = [];

        //  maintext
        if (parsed.maintext && !logs.some(log => log.content === parsed.maintext)) {
          newLogs.push({
            id: `initial-maintext-${parsed.messageId || Date.now()}`,
            sender: 'INNER_VOICE',
            content: parsed.maintext,
            timestamp: new Date().toLocaleTimeString('zh-CN'),
            type: 'narrative'
          });
        }

        // statustaskinventory 

        //  sum 
        if (parsed.sum) {
          setCurrentSum(parsed.sum);
        }

        // 
        if (newLogs.length > 0) {
          newLogs.forEach(log => addLog(log));
          console.log(` [Terminal]  ${newLogs.length} `);
        }

        // 
        if (parsed.options && parsed.options.length > 0) {
          setCurrentOptions(parsed.options);
        }

        //  
        if (parsed.messageId !== undefined && parsed.messageId !== null) {
          try {
            const userMessages = getChatMessages(parsed.messageId, { role: 'user' });
            const userMessageId = userMessages && userMessages.length > 0 ? userMessages[0].message_id : undefined;
            const assistantMessages = getChatMessages(parsed.messageId, { role: 'assistant' });
            const fullMessage = assistantMessages && assistantMessages.length > 0 ? assistantMessages[0].message : '';

            setCurrentMessageInfo({
              messageId: parsed.messageId,
              userMessageId: userMessageId,
              fullMessage: fullMessage,
            });
            console.log(' [Terminal] :', { messageId: parsed.messageId, userMessageId, hasFullMessage: !!fullMessage });
          } catch (error) {
            console.warn(' [Terminal] :', error);
          }
        }

        setHasLoadedInitialMessage(true);
      } catch (error) {
        console.error(' [Terminal] :', error);
        setHasLoadedInitialMessage(true); // 
      }
    };

    // 
    const timer = setTimeout(loadInitialMessage, 500);
    return () => clearTimeout(timer);
  }, [hasLoadedInitialMessage, logs, addLog]);

  const handleSend = async () => {
    if (!input.trim() || isGenerating) return;

    const userMsg = input;
    setInput('');

    // 
    addLog({
      id: Date.now().toString(),
      sender: 'USER',
      content: userMsg,
      timestamp: new Date().toLocaleTimeString('zh-CN'),
      type: 'info'
    });

    setIsGenerating(true);

    const request: RequestData = {
      type: 'custom',
      content: userMsg,
    };

    const success = await handleUnifiedRequest(request, {
      onDisableOptions: () => {
        setOptionsDisabled(true);
      },
      onShowGenerating: () => {
        setIsGenerating(true);
        setStreamingText('');
      },
      onHideGenerating: () => {
        setIsGenerating(false);
        setStreamingText('');
      },
      onEnableOptions: () => {
        setOptionsDisabled(false);
      },
      onError: (error: string) => {
        console.error(' [Terminal] :', error);
        //  toast 
        //  unifiedRequestHandler 
        setIsGenerating(false);
        setOptionsDisabled(false);
      },
      onRefreshStory: async () => {
        console.log(' [Terminal] ');
        try {
          await readGameData();
          const parsed = loadFromLatestMessage();

          if (parsed.options && parsed.options.length > 0) {
            setCurrentOptions(parsed.options);
          } else {
            setCurrentOptions([]);
          }

          if (parsed.maintext) {
            addLog({
              id: `maintext-${Date.now()}`,
              sender: 'INNER_VOICE',
              content: parsed.maintext,
              timestamp: new Date().toLocaleTimeString('zh-CN'),
              type: 'narrative'
            });
          }

          if (parsed.sum) {
            setCurrentSum(parsed.sum);
          }

          // 
          if (parsed.messageId !== undefined && parsed.messageId !== null) {
            try {
              const userMessages = getChatMessages(parsed.messageId, { role: 'user' });
              const userMessageId = userMessages && userMessages.length > 0 ? userMessages[0].message_id : undefined;
              const assistantMessages = getChatMessages(parsed.messageId, { role: 'assistant' });
              const fullMessage = assistantMessages && assistantMessages.length > 0 ? assistantMessages[0].message : '';

              setCurrentMessageInfo({
                messageId: parsed.messageId,
                userMessageId: userMessageId,
                fullMessage: fullMessage,
              });
            } catch (error) {
              console.warn(' [Terminal] :', error);
            }
          }
        } catch (error) {
          console.error(' [Terminal] :', error);
        }
      },
      onStreamingUpdate: (text: string) => {
        setStreamingText(text);
      },
      onRefreshVariables: () => {
        //  App 
      },
      onRestoreInput: (text: string) => {
        // 
        setInput(text);
        console.log(' [Terminal] ');
      },
    });

    if (!success) {
      setIsGenerating(false);
      setOptionsDisabled(false);
    } else {
      setStreamingText(''); // 
    }
  };

  // 
  const handleOptionClick = useCallback(async (option: Option) => {
    const request: RequestData = {
      type: 'option',
      content: option.text,
    };

    console.log(' [Terminal] :', option.text);

    const success = await handleUnifiedRequest(request, {
      onDisableOptions: () => {
        setOptionsDisabled(true);
        console.log(' [Terminal] ');
      },
      onShowGenerating: () => {
        setIsGenerating(true);
        setStreamingText('');
        console.log(' [Terminal] ');
      },
      onHideGenerating: () => {
        setIsGenerating(false);
        setStreamingText('');
        console.log(' [Terminal] ');
      },
      onEnableOptions: () => {
        setOptionsDisabled(false);
        console.log(' [Terminal] ');
      },
      onError: (error: string) => {
        console.error(' [Terminal] :', error);
        addLog({
          id: `error-${Date.now()}`,
          sender: 'SYSTEM',
          content: `: ${error}`,
          timestamp: new Date().toLocaleTimeString('zh-CN'),
          type: 'danger'
        });
        setIsGenerating(false);
        setOptionsDisabled(false);
      },
      onRefreshStory: async () => {
        console.log(' [Terminal] ');
        try {
          //  App 
          await readGameData();

          // 
          const parsed = loadFromLatestMessage();

          // 
          if (parsed.options && parsed.options.length > 0) {
            setCurrentOptions(parsed.options);
          } else {
            setCurrentOptions([]);
          }

          //  maintext
          if (parsed.maintext) {
            addLog({
              id: `maintext-${Date.now()}`,
              sender: 'INNER_VOICE',
              content: parsed.maintext,
              timestamp: new Date().toLocaleTimeString('zh-CN'),
              type: 'narrative'
            });
          }

          // statustaskinventory 

          if (parsed.sum) {
            setCurrentSum(parsed.sum);
          }

          //  
          if (parsed.messageId !== undefined && parsed.messageId !== null) {
            try {
              const userMessages = getChatMessages(parsed.messageId, { role: 'user' });
              const userMessageId = userMessages && userMessages.length > 0 ? userMessages[0].message_id : undefined;
              const assistantMessages = getChatMessages(parsed.messageId, { role: 'assistant' });
              const fullMessage = assistantMessages && assistantMessages.length > 0 ? assistantMessages[0].message : '';

              setCurrentMessageInfo({
                messageId: parsed.messageId,
                userMessageId: userMessageId,
                fullMessage: fullMessage,
              });
              console.log(' [Terminal] :', { messageId: parsed.messageId, userMessageId, hasFullMessage: !!fullMessage });
            } catch (error) {
              console.warn(' [Terminal] :', error);
            }
          }
        } catch (error) {
          console.error(' [Terminal] :', error);
        }
      },
      onStreamingUpdate: (text: string) => {
        setStreamingText(text);
        console.log(' [Terminal] :', text.substring(0, 50) + '...');
      },
      onRefreshVariables: () => {
        console.log(' [Terminal] ');
        //  App 
      },
    });

    if (success) {
      setStreamingText(''); // 
    }
  }, [addLog]);

  // 
  useEffect(() => {
    if (isGenerating || optionsDisabled) return;

    const refreshOptionsAndSum = () => {
      try {
        const parsed = loadFromLatestMessage();
        if (parsed.options && parsed.options.length > 0) {
          setCurrentOptions(parsed.options);
        }
        if (parsed.sum) {
          setCurrentSum(parsed.sum);
        }
      } catch (error) {
        console.warn(' [Terminal] :', error);
      }
    };

    // 
    const timer = setTimeout(refreshOptionsAndSum, 500);
    return () => clearTimeout(timer);
  }, [logs, isGenerating, optionsDisabled]);

  // 
  const handleLongPressStart = (event: React.MouseEvent | React.TouchEvent) => {
    if (
      contextMenu ||
      !currentMessageInfo.fullMessage ||
      currentMessageInfo.messageId === undefined ||
      currentMessageInfo.messageId === null ||
      isGenerating
    ) {
      return;
    }

    longPressTimerRef.current = window.setTimeout(() => {
      const clientX = 'touches' in event ? event.touches[0].clientX : event.clientX;
      const clientY = 'touches' in event ? event.touches[0].clientY : event.clientY;
      setContextMenu({ x: clientX, y: clientY });
      longPressTimerRef.current = null;
    }, 500);
  };

  const handleLongPressEnd = () => {
    if (longPressTimerRef.current) {
      clearTimeout(longPressTimerRef.current);
      longPressTimerRef.current = null;
    }
  };

  // roll
  const handleRegenerate = async () => {
    if (
      currentMessageInfo.messageId === undefined ||
      currentMessageInfo.messageId === null ||
      currentMessageInfo.userMessageId === undefined ||
      currentMessageInfo.userMessageId === null
    ) {
      alert('');
      setContextMenu(null);
      return;
    }

    try {
      setIsGenerating(true);
      setContextMenu(null);

      // user
      const userMessages = getChatMessages(currentMessageInfo.userMessageId, { role: 'user' });
      if (!userMessages || userMessages.length === 0) {
        throw new Error('');
      }
      const userMessageText = userMessages[0].message || '';

      // assistant
      await deleteChatMessages([currentMessageInfo.messageId], { refresh: 'none' });

      // narrative
      const narrativeLogs = logs.filter(log => log.sender === 'INNER_VOICE' && log.type === 'narrative');
      if (narrativeLogs.length > 0) {
        console.log(' [Terminal]  narrative ');
      }

      // 
      const success = await handleUnifiedRequest({ type: 'custom', content: userMessageText }, {
        onDisableOptions: () => setIsGenerating(true),
        onShowGenerating: () => setIsGenerating(true),
        onHideGenerating: () => setIsGenerating(false),
        onEnableOptions: () => setIsGenerating(false),
        onRefreshStory: async () => {
          await readGameData();
          const parsed = loadFromLatestMessage();
          if (parsed.maintext) {
            addLog({
              id: `maintext-${Date.now()}`,
              sender: 'INNER_VOICE',
              content: parsed.maintext,
              timestamp: new Date().toLocaleTimeString('zh-CN'),
              type: 'narrative'
            });
          }
          if (parsed.messageId !== undefined && parsed.messageId !== null) {
            try {
              const userMessages = getChatMessages(parsed.messageId, { role: 'user' });
              const userMessageId = userMessages && userMessages.length > 0 ? userMessages[0].message_id : undefined;
              const assistantMessages = getChatMessages(parsed.messageId, { role: 'assistant' });
              const fullMessage = assistantMessages && assistantMessages.length > 0 ? assistantMessages[0].message : '';
              setCurrentMessageInfo({
                messageId: parsed.messageId,
                userMessageId: userMessageId,
                fullMessage: fullMessage,
              });
            } catch (error) {
              console.warn(' [Terminal] :', error);
            }
          }
        },
        onError: (error) => {
          setIsGenerating(false);
          alert(`: ${error}`);
        },
      });

      if (!success) {
        setIsGenerating(false);
      }
    } catch (error) {
      setIsGenerating(false);
      alert(`: ${error instanceof Error ? error.message : String(error)}`);
    }
  };

  // 
  const handleEdit = () => {
    if (
      currentMessageInfo.messageId === undefined ||
      currentMessageInfo.messageId === null ||
      !currentMessageInfo.fullMessage
    ) {
      alert('');
      setContextMenu(null);
      return;
    }

    try {
      const maintextMatch = currentMessageInfo.fullMessage.match(/<maintext>([\s\S]*?)<\/maintext>/i);
      if (!maintextMatch) {
        alert('');
        return;
      }

      setEditingMessage({
        messageId: currentMessageInfo.messageId,
        currentText: maintextMatch[1].trim(),
        fullMessage: currentMessageInfo.fullMessage,
      });
      setContextMenu(null);
    } catch (error) {
      alert(`: ${error instanceof Error ? error.message : String(error)}`);
    }
  };

  // 
  const handleSaveEdit = async () => {
    if (!editingMessage) return;

    try {
      const { messageId, currentText, fullMessage } = editingMessage;
      const updatedMessage = fullMessage.replace(
        /<maintext>[\s\S]*?<\/maintext>/i,
        `<maintext>${currentText}</maintext>`
      );

      await setChatMessages(
        [{ message_id: messageId, message: updatedMessage }],
        { refresh: 'affected' }
      );

      setEditingMessage(null);

      // 
      setTimeout(async () => {
        await readGameData();
        const parsed = loadFromLatestMessage();
        if (parsed.maintext) {
          // 
          window.location.reload();
        }
      }, 100);
    } catch (error) {
      alert(`: ${error instanceof Error ? error.message : String(error)}`);
    }
  };

  // 
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (contextMenu) {
        const target = event.target as HTMLElement;
        const menuElement = target.closest('.context-menu');
        const textElement = target.closest('.maintext-container');
        if (!menuElement && !textElement) {
          setContextMenu(null);
        }
      }
    };

    if (contextMenu) {
      const timer = setTimeout(() => {
        document.addEventListener('click', handleClickOutside, true);
      }, 300);

      return () => {
        clearTimeout(timer);
        document.removeEventListener('click', handleClickOutside, true);
      };
    }
  }, [contextMenu]);

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <div className="flex flex-col h-full min-h-0 bg-void/80 border-x border-border-dim relative overflow-hidden group backdrop-blur-md">

      {/* Narrative Feed */}
      <div
        ref={scrollRef}
        className="flex-1 overflow-y-auto min-h-0 p-4 md:p-8 space-y-6 font-sans text-base md:text-lg leading-relaxed scroll-smooth"
      >
        {logs.length === 0 && (
          <div className="flex flex-col items-center justify-center h-full text-gray-600 space-y-4 opacity-50">
            <Ghost size={48} className="animate-pulse" />
            <div className="text-center font-mono text-sm">Waiting for Neural Input...</div>
          </div>
        )}

        {logs.map((log) => {
          const isMaintext = log.sender === 'INNER_VOICE' && log.type === 'narrative';
          const canLongPress = isMaintext && 
            currentMessageInfo.messageId !== undefined && 
            currentMessageInfo.messageId !== null && 
            !isGenerating;

          return (
            <div key={log.id} className={`flex flex-col animate-in fade-in slide-in-from-bottom-4 duration-500 ${log.sender === 'USER' ? 'items-end' : 'items-start'}`}>
              <div className={`flex items-center gap-2 text-[10px] uppercase tracking-widest mb-1 font-mono ${log.sender === 'USER' ? 'text-cog-cyan/70' : 'text-purple-400/70'
                }`}>
                <span>{log.sender}</span>
                <span className="opacity-50">{log.timestamp}</span>
              </div>
              <div 
                className={`maintext-container relative max-w-full md:max-w-[90%] p-4 rounded-sm border ${log.type === 'danger' ? 'border-panic-red/30 bg-panic-red/5' :
                  log.type === 'success' ? 'border-bio-green/30 bg-bio-green/5' :
                    log.sender === 'USER' ? 'border-cog-cyan/30 bg-cog-cyan/5' :
                      'border-transparent bg-transparent pl-4 border-l-2 border-l-purple-500/50'
                  }`}
                onMouseDown={(e) => {
                  if (!canLongPress || contextMenu) return;
                  e.preventDefault();
                  e.stopPropagation();
                  if (currentMessageInfo.messageId !== undefined && currentMessageInfo.messageId !== null) {
                    handleLongPressStart(e);
                  }
                }}
                onMouseUp={(e) => {
                  e.stopPropagation();
                  handleLongPressEnd();
                }}
                onTouchStart={(e) => {
                  if (!canLongPress || contextMenu) return;
                  if (currentMessageInfo.messageId !== undefined && currentMessageInfo.messageId !== null) {
                    handleLongPressStart(e);
                  }
                }}
                onTouchEnd={(e) => {
                  handleLongPressEnd();
                }}
                onTouchCancel={(e) => {
                  handleLongPressEnd();
                }}
                style={{ cursor: canLongPress ? 'pointer' : 'default' }}
              >
                <div className="whitespace-pre-wrap font-serif tracking-wide text-gray-200">
                  {log.content}
                </div>
              </div>
            </div>
          );
        })}

        {isThinking && (
          <div className="flex items-center gap-3 text-cog-cyan opacity-70 p-4 animate-pulse">
            <Sparkles size={16} />
            <span className="text-xs font-mono tracking-widest">CALCULATING...</span>
          </div>
        )}

        {/*  */}
        {streamingText && (
          <div className="flex flex-col animate-in fade-in slide-in-from-bottom-4 duration-500 items-start">
            <div className="flex items-center gap-2 text-[10px] uppercase tracking-widest mb-1 font-mono text-purple-400/70">
              <span>INNER_VOICE</span>
              <span className="opacity-50">{new Date().toLocaleTimeString('zh-CN')}</span>
            </div>
            <div className="relative max-w-full md:max-w-[90%] p-4 rounded-sm border border-transparent bg-transparent pl-4 border-l-2 border-l-purple-500/50">
              <div className="whitespace-pre-wrap font-serif tracking-wide text-gray-200">
                {streamingText}
              </div>
            </div>
          </div>
        )}
      </div>

      {/*  */}
      {currentOptions.length > 0 && !isGenerating && (
        <div className="shrink-0 p-4 md:p-6 bg-black/90 backdrop-blur-md border-t border-border-dim z-20">
          <div className="max-w-4xl mx-auto">
            <button
              onClick={() => setOptionsExpanded(!optionsExpanded)}
              className="w-full text-left mb-3 flex items-center justify-between text-xs uppercase tracking-widest text-gray-500 font-mono hover:text-cog-cyan transition-colors"
            >
              <span> ({currentOptions.length})</span>
              <span className="text-cog-cyan">{optionsExpanded ? '' : ''}</span>
            </button>
            {optionsExpanded && (
              <div className="flex flex-col gap-2 animate-in fade-in slide-in-from-top-2 duration-300">
                {currentOptions.map((option, index) => (
                  <button
                    key={`${option.id}-${index}`}
                    onClick={() => handleOptionClick(option)}
                    disabled={optionsDisabled || isGenerating}
                    className="game-option-btn"
                  >
                    <div className="flex items-center gap-3">
                      <span className="text-cog-cyan font-mono text-sm font-bold">{option.id}.</span>
                      <span className="text-gray-200 font-sans">{option.text}</span>
                    </div>
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
      )}

      {/*  */}
      {isGenerating && (
        <div className="shrink-0 p-4 md:p-6 bg-black/90 backdrop-blur-md border-t border-border-dim z-20">
          <div className="max-w-4xl mx-auto">
            <div className="flex items-center gap-3 text-cog-cyan opacity-70 animate-pulse">
              <Sparkles size={16} />
              <span className="text-xs font-mono tracking-widest">...</span>
            </div>
          </div>
        </div>
      )}

      {/* Input Area */}
      <div className="shrink-0 p-4 md:p-6 bg-black/90 backdrop-blur-md border-t border-border-dim z-20">
        <div className="relative max-w-4xl mx-auto">
          <div className="relative flex items-end gap-2 bg-black border border-border-dim p-2 rounded">
            <textarea
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={handleKeyDown}
              placeholder="..."
              className="w-full bg-transparent text-gray-200 font-sans text-base p-3 min-h-[50px] max-h-[120px] resize-none outline-none placeholder-gray-700"
            />
            <button
              onClick={handleSend}
              disabled={isThinking || !input.trim()}
              className="p-3 mb-1 text-cog-cyan hover:bg-cog-cyan/10 rounded transition-colors disabled:opacity-30"
            >
              <Send size={20} />
            </button>
          </div>
        </div>
      </div>

      {/*  */}
      {contextMenu && (
        <div
          className="context-menu fixed z-[2000] bg-black/95 border border-cog-cyan/30 shadow-lg min-w-[200px]"
          style={{
            left: `${Math.min(contextMenu.x, window.innerWidth - 220)}px`,
            top: `${Math.min(contextMenu.y, window.innerHeight - 100)}px`,
          }}
          onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
          }}
        >
          <div className="flex justify-between items-center p-2 border-b border-cog-cyan/20">
            <span className="text-xs text-gray-400"></span>
            <button 
              onClick={() => setContextMenu(null)} 
              className="text-gray-500 hover:text-white transition-colors"
            >
              <X size={16} />
            </button>
          </div>
          <button
            onClick={(e) => {
              e.stopPropagation();
              if (!isGenerating) {
                handleRegenerate();
              }
            }}
            disabled={isGenerating}
            className="w-full text-left px-4 py-2 text-sm text-gray-300 hover:bg-cog-cyan/10 hover:text-cog-cyan transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isGenerating ? ' ...' : ' roll'}
          </button>
          <button
            onClick={(e) => {
              e.stopPropagation();
              if (!isGenerating) {
                handleEdit();
              }
            }}
            disabled={isGenerating}
            className="w-full text-left px-4 py-2 text-sm text-gray-300 hover:bg-cog-cyan/10 hover:text-cog-cyan transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
             
          </button>
        </div>
      )}

      {/*  */}
      {editingMessage && (
        <div
          className="fixed inset-0 z-[3000] flex items-center justify-center bg-black/80 backdrop-blur-sm p-4"
          onClick={() => setEditingMessage(null)}
        >
          <div 
            className="bg-black/95 border border-cog-cyan/30 rounded-lg w-full max-w-3xl max-h-[80vh] flex flex-col"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex justify-between items-center p-4 border-b border-cog-cyan/20">
              <h2 className="text-lg font-tech text-white"></h2>
              <button 
                onClick={() => setEditingMessage(null)} 
                className="text-gray-500 hover:text-white transition-colors"
              >
                <X size={20} />
              </button>
            </div>
            <div className="p-6 flex-1 overflow-y-auto">
              <textarea
                value={editingMessage.currentText}
                onChange={(e) =>
                  setEditingMessage({ ...editingMessage, currentText: e.target.value })
                }
                className="w-full h-[400px] p-4 bg-black/50 border border-cog-cyan/20 text-cog-cyan/90 font-mono text-sm resize-none outline-none focus:border-cog-cyan/50 transition-colors leading-relaxed"
              />
              <div className="flex gap-3 mt-4">
                <button
                  onClick={handleSaveEdit}
                  className="px-6 py-2 bg-cog-cyan text-black font-bold hover:bg-cog-cyan/80 transition-colors"
                >
                  
                </button>
                <button
                  onClick={() => setEditingMessage(null)}
                  className="px-6 py-2 bg-gray-700 text-gray-300 hover:bg-gray-600 transition-colors"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
