
export enum ViewState {
  DASHBOARD = 'DASHBOARD',
  ENTITY_ARCHIVE = 'ENTITY_ARCHIVE',
  MEMORY_BANK = 'MEMORY_BANK',
  MAP = 'MAP',
  GEAR = 'GEAR',
  LOGS = 'LOGS',
  STATUS = 'STATUS',
  CHARACTERS = 'CHARACTERS',
  AGENTS = 'AGENTS',
  TALENT_TREE = 'TALENT_TREE'
}

export interface LogEntry {
  id: string;
  sender: 'SYSTEM' | 'USER' | 'ENTITY' | 'INNER_VOICE';
  content: string;
  timestamp: string;
  type: 'info' | 'warning' | 'danger' | 'success' | 'narrative';
}

// --- World System ---
export interface WorldInfo {
  currentTime: string;
  currentDate: string;
  recentEvents: string[];
}

// --- Dynamic Systems ---

export interface Item {
  id: string;
  name: string;
  description: string;
  quantity: number;
  type: 'WEAPON' | 'ARMOR' | 'CONSUMABLE' | 'MISC' | 'EGO';
  tags?: string[];
}

export interface Quest {
  id: string;
  title: string;
  description: string;
  status: 'ACTIVE' | 'COMPLETED' | 'FAILED';
  objectives: string[];
}

export interface Character {
  id: string;
  name: string;
  title: string;
  faction: string; 
  relation: number; // 0-100
  status: 'ALIVE' | 'MISSING' | 'DECEASED' | 'UNKNOWN';
  description: string;
  tags: string[]; // e.g. ["Fixer", "Dealer"]
}

export interface Entity {
  id: string;
  code: string; // e.g. "O-03-03"
  name: string;
  riskLevel: 'ZAYIN' | 'TETH' | 'HE' | 'WAW' | 'ALEPH';
  description: string;
  observationLevel: number;
  workResults: {
    instinct: string;
    insight: string;
    attachment: string;
    repression: string;
  };
  status: 'CONTAINED' | 'BREACHED' | 'SUPPRESSED';
}

// --- Player State ---

export type BackgroundType = 'CORP_REMNANT' | 'BACKSTREET_FIXER' | 'SYNDICATE_RAT' | 'WARP_PASSENGER';

export interface PlayerProfile {
  name: string;
  gender: string;
  age: number;
  background: BackgroundType;
  history: string;
  startingTrait: string;
}

export interface PlayerStats {
  // Identity
  name: string;
  title: string;
  
  // Vitals
  sanity: number;
  sanityMax: number;
  
  // World & Context
  world: WorldInfo;
  location: string;
  
  // Inventory & Economy
  credits: number;
  items: Item[]; 

  // Content Arrays
  activeTasks: Quest[];
  contacts: Character[]; // Generic list of NPCs/Agents
  entities: Entity[];    // Generic list of Abnormalities

  // Legacy/Flexible
  attributes: Record<string, number>;
  logs: LogEntry[];
  talents?: string[]; // ID
}

