import { parseInventory, parseStatus, parseTask } from './messageParser';
import { getGameMvuData } from './variableReader';

// 
declare function getChatMessages(
  range: string | number,
  options?: { role?: 'all' | 'system' | 'assistant' | 'user' },
): Array<{ message: string; message_id: number; role: string; data?: Record<string, any> }>;
declare function getLastMessageId(): number;
declare function createChatMessages(
  messages: Array<{ role: 'user' | 'assistant' | 'system'; message: string; data?: Record<string, any> }>,
  options?: { refresh?: 'none' | 'affected' | 'all' },
): Promise<void>;
declare function deleteChatMessages(message_ids: number[], { refresh }?: { refresh?: 'none' | 'affected' | 'all' }): Promise<void>;
declare function generate(config: { user_input: string; should_stream?: boolean }): Promise<string>;
declare function waitGlobalInitialized<T>(global: string): Promise<T>;
declare function eventOn<T extends string>(event_type: T, listener: (text: string, generation_id?: string) => void): { stop: () => void };
declare const iframe_events: {
  STREAM_TOKEN_RECEIVED_FULLY: string;
  GENERATION_STARTED: string;
  GENERATION_ENDED: string;
};
declare const Mvu: {
  getMvuData: (options: { type: 'message' | 'chat' | 'character' | 'global'; message_id?: number | 'latest' }) => {
    stat_data: Record<string, any>;
    display_data: Record<string, any>;
    delta_data: Record<string, any>;
  };
  parseMessage: (message: string, old_data: { stat_data: Record<string, any> }) => Promise<{ stat_data: Record<string, any> }>;
};

export type RequestType = 'option' | 'custom' | 'your-custom-type';

export interface RequestData {
  type: RequestType;
  content: string;
}

export interface UnifiedRequestCallbacks {
  onDisableOptions?: () => void;
  onShowGenerating?: () => void;
  onHideGenerating?: () => void;
  onEnableOptions?: () => void;
  onError?: (error: string) => void;
  onRefreshStory?: () => void;
  onStreamingUpdate?: (text: string) => void;
  onRefreshVariables?: () => void;
  onRestoreInput?: (text: string) => void;
}

/**
 * 
 */
function buildRequestPrompt(request: RequestData): string {
  if (request.type === 'option') {
    // 
    return request.content;
  } else if (request.type === 'custom') {
    // 
    return request.content;
  } else {
    // 
    return request.content;
  }
}

/**
 *  MVU  user 
 */
async function getBaseMvuData(): Promise<{ stat_data: Record<string, any>; display_data?: Record<string, any>; delta_data?: Record<string, any> }> {
  try {
    //  variableReader  MVU 
    const mvuData = await getGameMvuData();
    if (mvuData && mvuData.stat_data) {
      //  Mvu.getMvuData  delta_data
      let delta_data = {};
      try {
        if (typeof Mvu !== 'undefined') {
          const fullMvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
          delta_data = fullMvuData?.delta_data || {};
        }
      } catch (err) {
        // 
      }
      
      return {
        stat_data: mvuData.stat_data || {},
        display_data: mvuData.display_data || {},
        delta_data: delta_data,
      };
    }
  } catch (error) {
    console.warn(' [unifiedRequestHandler]  MVU ', error);
  }

  // 
  try {
    if (typeof Mvu !== 'undefined') {
      const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
      if (mvuData && mvuData.stat_data) {
        return {
          stat_data: mvuData.stat_data || {},
          display_data: mvuData.display_data || {},
          delta_data: mvuData.delta_data || {},
        };
      }
    }
  } catch (error) {
    console.warn(' [unifiedRequestHandler]  MVU ', error);
  }

  // 
  return { stat_data: {}, display_data: {}, delta_data: {} };
}

/**
 *  <thinking>  <think> 
 */
function removeThinkingTags(message: string): string {
  if (!message) return '';
  
  //  <thinking>  <think> 
  let cleaned = message.replace(/<thinking>[\s\S]*?<\/thinking>/gi, '');
  cleaned = cleaned.replace(/<think>[\s\S]*?<\/redacted_reasoning>/gi, '');
  
  // 
  const thinkingStart = cleaned.search(/<thinking>/i);
  if (thinkingStart !== -1) {
    cleaned = cleaned.substring(0, thinkingStart);
  }
  const redactedStart = cleaned.search(/<think>/i);
  if (redactedStart !== -1) {
    cleaned = cleaned.substring(0, redactedStart);
  }
  
  return cleaned.trim();
}

/**
 *  thinking 
 */
function removeThinkingTagsFromStream(fullText: string): string {
  return removeThinkingTags(fullText);
}

/**
 *  <maintext><option> 
 */
function validateMessage(message: string): boolean {
  if (!message || !message.trim()) return false;
  
  // maintext 
  const hasMaintext = /<maintext>[\s\S]*?<\/maintext>/i.test(message);
  
  //  maintext option 
  return hasMaintext;
}

/**
 *  <maintext> 
 */
function extractLastMaintext(message: string): string {
  const matches = message.match(/<maintext>([\s\S]*?)<\/maintext>/gi);
  if (!matches || matches.length === 0) return '';
  const lastMatch = matches[matches.length - 1];
  const content = lastMatch.match(/<maintext>([\s\S]*?)<\/maintext>/i);
  return content ? content[1].trim() : '';
}

/**
 *  <option> 
 */
function extractLastOption(message: string): string {
  const matches = message.match(/<option>([\s\S]*?)<\/option>/gi);
  if (!matches || matches.length === 0) return '';
  const lastMatch = matches[matches.length - 1];
  const content = lastMatch.match(/<option>([\s\S]*?)<\/option>/i);
  return content ? content[1].trim() : '';
}

/**
 *  <sum> 
 */
function extractLastSum(message: string): string {
  const matches = message.match(/<sum>([\s\S]*?)<\/sum>/gi);
  if (!matches || matches.length === 0) return '';
  const lastMatch = matches[matches.length - 1];
  const content = lastMatch.match(/<sum>([\s\S]*?)<\/sum>/i);
  return content ? content[1].trim() : '';
}

/**
 *  <UpdateVariable> 
 */
function extractLastUpdateVariable(message: string): string {
  const matches = message.match(/<UpdateVariable>([\s\S]*?)<\/UpdateVariable>/gi);
  if (!matches || matches.length === 0) return '';
  const lastMatch = matches[matches.length - 1];
  const content = lastMatch.match(/<UpdateVariable>([\s\S]*?)<\/UpdateVariable>/i);
  return content ? content[1].trim() : '';
}

/**
 * 
 */
export async function handleUnifiedRequest(
  request: RequestData,
  callbacks: UnifiedRequestCallbacks,
): Promise<boolean> {
  let userMessageId: number | null = null;
  let streamingHandler: { stop: () => void } | null = null;
  
  //  try  prompt catch 
  const prompt = buildRequestPrompt(request);

  try {
    console.log(` [unifiedRequestHandler] : ${request.type} - ${request.content.substring(0, 50)}...`);

    // 1. 
    if (callbacks.onDisableOptions) {
      callbacks.onDisableOptions();
    }

    // 2. ""
    if (callbacks.onShowGenerating) {
      callbacks.onShowGenerating();
    }
    console.log(` [unifiedRequestHandler] : ${prompt.substring(0, 100)}...`);

    // 4.  MVU  user 
    const mvu_data = await getBaseMvuData();
    console.log(' [unifiedRequestHandler]  MVU ');

    // 5.  user  MVU 
    await createChatMessages(
      [
        {
          role: 'user',
          message: prompt,
          data: mvu_data,
        },
      ],
      {
        refresh: 'none',
      },
    );

    // 6.  user  ID
    userMessageId = getLastMessageId();
    console.log(` [unifiedRequestHandler]  user ID: ${userMessageId}`);

    // 7.  generate 
    if (typeof eventOn !== 'undefined' && typeof iframe_events !== 'undefined' && iframe_events.STREAM_TOKEN_RECEIVED_FULLY) {
      streamingHandler = eventOn(iframe_events.STREAM_TOKEN_RECEIVED_FULLY, (fullText: string) => {
        const cleanedText = removeThinkingTagsFromStream(fullText);
        if (cleanedText && cleanedText.trim().length > 0) {
          const maintextMatch = cleanedText.match(/<maintext>([\s\S]*?)(?:<\/maintext>|$)/i);
          if (maintextMatch && maintextMatch[1]) {
            if (callbacks.onStreamingUpdate) {
              callbacks.onStreamingUpdate(maintextMatch[1].trim());
            }
          }
        }
      });
      console.log(' [unifiedRequestHandler] ');
    }

    // 8.  generate  LLM 
    console.log(' [unifiedRequestHandler]  generate...');
    const result = await generate({
      user_input: prompt,
      should_stream: true,
    });
    console.log(` [unifiedRequestHandler] generate : ${result.length}`);

    // 9. 
    if (streamingHandler) {
      streamingHandler.stop();
      streamingHandler = null;
    }

    // 10.  <thinking> 
    const cleanedResult = removeThinkingTags(result);
    console.log(` [unifiedRequestHandler]  thinking : ${cleanedResult.length}`);

    // 11. 
    if (!cleanedResult || !validateMessage(cleanedResult)) {
      console.error(' [unifiedRequestHandler]  <maintext>');
      console.error(' [unifiedRequestHandler] :', cleanedResult);
      // 
      (window as any).__lastUnifiedDebug = {
        original: result.substring(0, 2000),
        cleaned: cleanedResult.substring(0, 2000),
        originalLength: result.length,
        cleanedLength: cleanedResult.length,
      };
      //  user 
      if (userMessageId !== null) {
        await deleteChatMessages([userMessageId], { refresh: 'none' });
      }
      if (callbacks.onHideGenerating) callbacks.onHideGenerating();
      if (callbacks.onEnableOptions) callbacks.onEnableOptions();
      if (callbacks.onError) callbacks.onError('');
      if (callbacks.onRestoreInput) {
        callbacks.onRestoreInput(prompt);
      }
      return false;
    }
    
    //  option  option 
    const hasOption = /<option>/i.test(cleanedResult);
    if (!hasOption) {
      console.warn(' [unifiedRequestHandler]  <option> ');
    }

    // 12. 
    const maintext = extractLastMaintext(cleanedResult);
    let option = extractLastOption(cleanedResult);
    const sum = extractLastSum(cleanedResult);
    const updateVariable = extractLastUpdateVariable(cleanedResult);

    //  option 
    if (!option || option.trim().length === 0) {
      option = '';
      console.warn(' [unifiedRequestHandler] option ');
    }

    console.log(` [unifiedRequestHandler] : maintext(${maintext.length}), option(${option.length}), sum(${sum.length}), updateVariable(${updateVariable.length})`);

    // 13. 
    let finalMessage = `<maintext>${maintext}</maintext>\n\n<option>${option}</option>`;
    
    // 
    const status = parseStatus(cleanedResult);
    if (status) {
      finalMessage = `<status>${status}</status>\n\n${finalMessage}`;
    }
    
    const task = parseTask(cleanedResult);
    if (task) {
      finalMessage = `${finalMessage}\n\n<task>${task}</task>`;
    }
    
    const inventory = parseInventory(cleanedResult);
    if (inventory) {
      finalMessage = `${finalMessage}\n\n<inventory>${inventory}</inventory>`;
    }
    
    if (sum) {
      finalMessage = `${finalMessage}\n\n<sum>${sum}</sum>`;
    }
    
    if (updateVariable) {
      finalMessage = `${finalMessage}\n\n<UpdateVariable>${updateVariable}</UpdateVariable>`;
    }

    // 14.  MVU 
    try {
      await waitGlobalInitialized('Mvu');
      console.log(' [unifiedRequestHandler] MVU ');
    } catch (error) {
      console.warn(' [unifiedRequestHandler]  MVU ', error);
    }

    // 15.  MVU 
    const base = await getBaseMvuData();

    // 16.  MVU 
    let finalData = base;
    if (typeof Mvu !== 'undefined') {
      try {
        const parsed = await Mvu.parseMessage(finalMessage, base);
        if (parsed && parsed.stat_data) {
          finalData = {
            stat_data: parsed.stat_data || base.stat_data,
            display_data: base.display_data || {},
            delta_data: base.delta_data || {},
          };
          console.log(' [unifiedRequestHandler] MVU ');
        }
      } catch (err) {
        console.warn(' [unifiedRequestHandler]  MVU ', err);
      }
    }

    // 17.  assistant 
    await createChatMessages(
      [
        {
          role: 'assistant',
          message: finalMessage,
          data: finalData,
        },
      ],
      {
        refresh: 'none', // 
      },
    );
    console.log(' [unifiedRequestHandler]  assistant ');

    // 18. 
    try {
      const { checkAndUpdateChronicle } = await import('./chronicleUpdater');
      await checkAndUpdateChronicle();
      console.log(' [unifiedRequestHandler] ');
    } catch (error) {
      console.warn(' [unifiedRequestHandler] ', error);
    }

    // 19. 
    if (callbacks.onHideGenerating) {
      callbacks.onHideGenerating();
    }

    // 20. 
    setTimeout(() => {
      if (callbacks.onRefreshStory) {
        callbacks.onRefreshStory();
      }
      if (callbacks.onRefreshVariables) {
        callbacks.onRefreshVariables();
      }
    }, 300);

    // 21. 
    if (callbacks.onEnableOptions) {
      callbacks.onEnableOptions();
    }

    console.log(' [unifiedRequestHandler] ');
    return true;
  } catch (error) {
    console.error(' [unifiedRequestHandler] :', error);
    
    // 
    if (streamingHandler) {
      try {
        streamingHandler.stop();
      } catch (err) {
        // 
      }
    }

    //  user 
    if (userMessageId !== null) {
      try {
        await deleteChatMessages([userMessageId], { refresh: 'none' });
      } catch (err) {
        console.warn(' [unifiedRequestHandler]  user ', err);
      }
    }

    // 
    if (callbacks.onHideGenerating) {
      callbacks.onHideGenerating();
    }

    // 
    if (callbacks.onEnableOptions) {
      callbacks.onEnableOptions();
    }

    // 
    if (callbacks.onError) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      callbacks.onError(`: ${errorMessage}`);
    }

    // 
    if (callbacks.onRestoreInput) {
      callbacks.onRestoreInput(prompt);
    }

    return false;
  }
}
