import React from 'react';
import { createRoot } from 'react-dom/client';
import App from './App';
import './index.css';
import { initializeTheme } from './utils/themeManager';

// 
declare function getLastMessageId(): number;

//  jQuery DOMContentLoaded
const mount = () => {
  // 
  initializeTheme();

  const rootElement = document.getElementById('root');
  if (!rootElement) {
    console.error(' [index.tsx]  root ');
    return;
  }

  const root = createRoot(rootElement);

  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );

  //  jQuery  pagehide 
  const jq = (window as any).$;
  if (typeof jq === 'function') {
    jq(window).on('pagehide', () => {
      root.unmount();
    });
  }
};

//  iframe  jQuery jQuery DOMContentLoaded/
const jq = (window as any).$;
const parentJq = (window.parent as any && (window.parent as any).$) as any;

if (typeof jq === 'function') {
  jq(mount);
} else if (typeof parentJq === 'function') {
  parentJq(mount);
} else {
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', mount);
  } else {
    mount();
  }
}