// 
//
// 
// -  assistant  <sum>
// - """"

declare function getChatMessages(
  range: string | number,
  options?: { role?: 'all' | 'system' | 'assistant' | 'user' },
): Array<{ message_id: number; role: string; message: string }>;

declare function getChatWorldbookName(chat_name: 'current'): string | null;
declare function getWorldbookNames(): string[];
declare function getWorldbook(worldbook_name: string): Promise<any[]>;
declare function replaceWorldbook(
  worldbook_name: string,
  worldbook: any[],
  options?: { render?: 'debounced' | 'immediate' },
): Promise<void>;
declare function createWorldbookEntries(
  worldbook_name: string,
  new_entries: any[],
  options?: { render?: 'debounced' | 'immediate' },
): Promise<{ worldbook: any[]; new_entries: any[] }>;

const CHRONICLE_ENTRY_NAME = '';
const MAX_LINES = 200;

function extractSum(messageContent: string): string | null {
  const sumMatch = messageContent.match(/<sum>([\s\S]*?)<\/sum>/i);
  if (!sumMatch || !sumMatch[1]) return null;
  const sum = sumMatch[1].trim();
  return sum.length > 0 ? sum : null;
}

function getNowStamp(): string {
  try {
    return new Date().toLocaleString('zh-CN', { hour12: false });
  } catch {
    return String(Date.now());
  }
}

function appendChronicleLine(existing: string, line: string): string {
  const normalized = (existing || '').trim();
  const combined = normalized ? `${normalized}\n${line}` : line;
  const lines = combined.split('\n').filter(Boolean);
  if (lines.length <= MAX_LINES) return lines.join('\n');
  return lines.slice(lines.length - MAX_LINES).join('\n');
}

function findChronicleEntry(worldbook: any[]): any | null {
  if (!Array.isArray(worldbook)) return null;
  return (
    worldbook.find(e => e?.extra?.type === 'chronicle') ||
    worldbook.find(e => e?.name === CHRONICLE_ENTRY_NAME) ||
    worldbook.find(e => typeof e?.name === 'string' && e.name.includes(CHRONICLE_ENTRY_NAME)) ||
    null
  );
}

async function ensureChronicleEntry(worldbookName: string): Promise<any | null> {
  const worldbook = await getWorldbook(worldbookName);
  const existing = findChronicleEntry(worldbook);
  if (existing) return existing;

  if (typeof createWorldbookEntries !== 'undefined') {
    const created = await createWorldbookEntries(
      worldbookName,
      [
        {
          name: CHRONICLE_ENTRY_NAME,
          enabled: true,
          strategy: { type: 'constant', keys: [], keys_secondary: { logic: 'and_any', keys: [] }, scan_depth: 1 },
          position: { type: 'before_author_note', role: 'system', depth: 0, order: 0 },
          probability: 100,
          content: '',
          recursion: { prevent_incoming: true, prevent_outgoing: true, delay_until: null },
          effect: { sticky: null, cooldown: null, delay: null },
          extra: { type: 'chronicle' },
        },
      ],
      { render: 'debounced' },
    );
    return findChronicleEntry(created.worldbook);
  }

  return null;
}

/**
 * sum
 */
interface ChronicleEntry {
  floorNumber: number; // messageId / 2
  messageId: number;
  timestamp: string;
  sum: string;
  line: string;
}

function parseChronicleContent(content: string): ChronicleEntry[] {
  const entries: ChronicleEntry[] = [];
  const lines = content.split('\n').filter(line => line.trim());
  
  for (const line of lines) {
    // : - [#messageId] timestamp sum
    const match = line.match(/^-\s*\[#(\d+)\]\s+(.+?)\s+(.+)$/);
    if (match) {
      const messageId = parseInt(match[1], 10);
      const floorNumber = Math.floor(messageId / 2);
      const timestamp = match[2];
      const sum = match[3];
      
      entries.push({
        floorNumber,
        messageId,
        timestamp,
        sum,
        line,
      });
    }
  }
  
  return entries;
}

/**
 * 
 */
function formatChronicleEntry(floorNumber: number, messageId: number, timestamp: string, sum: string): string {
  return `- [#${messageId}] ${timestamp} ${sum}`;
}

/**
 *  <sum> 
 * 
 */
export async function checkAndUpdateChronicle(): Promise<void> {
  try {
    if (typeof getChatMessages === 'undefined') return;
    if (typeof getWorldbook === 'undefined' || typeof replaceWorldbook === 'undefined') return;

    //  assistant 
    let assistantMessages: Array<{ message_id: number; role: string; message: string }> = [];
    try {
      assistantMessages = getChatMessages('0-{{lastMessageId}}', { role: 'assistant' }) as any;
    } catch {
      try {
        assistantMessages = getChatMessages(-1, { role: 'assistant' }) as any;
      } catch {
        assistantMessages = [];
      }
    }
    if (!assistantMessages || assistantMessages.length === 0) return;

    const latestAssistant = assistantMessages[assistantMessages.length - 1];
    const messageContent = latestAssistant.message || '';
    const sumContent = extractSum(messageContent);
    if (!sumContent) return;

    const messageId = latestAssistant.message_id;
    const floorNumber = Math.floor(messageId / 2); // 

    // 
    let worldbookName: string | null = null;
    try {
      if (typeof getChatWorldbookName !== 'undefined') {
        worldbookName = getChatWorldbookName('current');
      }
    } catch {
      worldbookName = null;
    }
    if (!worldbookName) {
      try {
        const names = typeof getWorldbookNames !== 'undefined' ? getWorldbookNames() : [];
        worldbookName = names?.[0] ?? null;
      } catch {
        worldbookName = null;
      }
    }
    if (!worldbookName) return;

    // 
    const chronicleEntry = await ensureChronicleEntry(worldbookName);
    if (!chronicleEntry) return;

    const oldContent: string = chronicleEntry.content || '';
    const entries = parseChronicleContent(oldContent);
    
    // 
    const existingEntryIndex = entries.findIndex(e => e.floorNumber === floorNumber);
    const hasHigherFloor = entries.some(e => e.floorNumber > floorNumber);
    
    let newContent: string;
    
    if (existingEntryIndex !== -1 && hasHigherFloor) {
      // sumsum
      console.log(` [chronicleUpdater]  ${floorNumber}`);
      
      const now = getNowStamp();
      const newLine = formatChronicleEntry(floorNumber, messageId, now, sumContent);
      
      // 
      entries[existingEntryIndex] = {
        floorNumber,
        messageId,
        timestamp: now,
        sum: sumContent,
        line: newLine,
      };
      
      // 
      const filteredEntries = entries.filter(e => e.floorNumber <= floorNumber);
      
      // 
      filteredEntries.sort((a, b) => a.floorNumber - b.floorNumber);
      
      newContent = filteredEntries.map(e => e.line).join('\n');
    } else if (existingEntryIndex !== -1) {
      // 
      console.log(` [chronicleUpdater]  ${floorNumber} sum`);
      return;
    } else {
      // sum
      console.log(` [chronicleUpdater]  ${floorNumber} sum`);
      
      const now = getNowStamp();
      const newLine = formatChronicleEntry(floorNumber, messageId, now, sumContent);
      
      // N-1
      const insertIndex = entries.findIndex(e => e.floorNumber >= floorNumber);
      
      if (insertIndex === -1) {
        // 
        entries.push({
          floorNumber,
          messageId,
          timestamp: now,
          sum: sumContent,
          line: newLine,
        });
      } else {
        // N-1
        entries.splice(insertIndex, 0, {
          floorNumber,
          messageId,
          timestamp: now,
          sum: sumContent,
          line: newLine,
        });
      }
      
      // 
      entries.sort((a, b) => a.floorNumber - b.floorNumber);
      
      // 
      const lines = entries.map(e => e.line);
      if (lines.length > MAX_LINES) {
        newContent = lines.slice(lines.length - MAX_LINES).join('\n');
      } else {
        newContent = lines.join('\n');
      }
    }

    // 
    const worldbook = await getWorldbook(worldbookName);
    const updated = (worldbook || []).map(entry => {
      if (entry?.uid === chronicleEntry.uid) {
        return {
          ...entry,
          enabled: true,
          content: newContent,
          extra: { ...(entry.extra || {}), type: 'chronicle' },
        };
      }
      return entry;
    });

    await replaceWorldbook(worldbookName, updated, { render: 'debounced' });
    console.log(` [chronicleUpdater] ${worldbookName}  ${floorNumber} (#${messageId})`);
  } catch (error) {
    console.error(' :', error);
  }
}
