/**
 * 
 *  adventure 
 */

export type Theme = 'dark' | 'relaxed';

/**
 * 
 */
export function setTheme(theme: Theme): void {
  document.documentElement.setAttribute('data-theme', theme);
  try {
    localStorage.setItem('cognition-protocol-theme', theme);
    console.log(` [ThemeManager] : ${theme}`);
  } catch (error) {
    console.warn(' [ThemeManager]  localStorage:', error);
  }
}

/**
 * 
 */
export function getTheme(): Theme {
  try {
    const savedTheme = localStorage.getItem('cognition-protocol-theme') as Theme | null;
    if (savedTheme && (savedTheme === 'dark' || savedTheme === 'relaxed')) {
      return savedTheme;
    }
  } catch (error) {
    console.warn(' [ThemeManager]  localStorage :', error);
  }
  return 'dark'; // 
}

/**
 * 
 */
export function initializeTheme(): void {
  const theme = getTheme();
  setTheme(theme);
  console.log(` [ThemeManager] : ${theme}`);
}

/**
 *  dark  relaxed 
 */
export function toggleTheme(): Theme {
  const currentTheme = getTheme();
  const newTheme: Theme = currentTheme === 'dark' ? 'relaxed' : 'dark';
  setTheme(newTheme);
  return newTheme;
}
